import React from 'react';

const Button = props => {
	// Array padrão style dos botões.
	let model = [
		"py-2",
		"ml-2",
		"font-semibold",
		"focus:outline-none",
		"hover:opacity-50",
		"w-full",
		"text-lg",
		"sm:w-40",
		"px-10",
		"rounded",
		"rounded-lg"
	];


	// Config Outline
	let model1 = [`${props.bg}`,"text-white"];
	let outline = model.concat(model1);

	// Config  Outline-none
	let model2 = [`${props.bg}`,"border","border-green-500","text-green-500"];
	const outlineNone = model.concat(model2);

	function retur() {
		switch(props.style) {
			case "outline":
				return outline.join(" ");
				break;
			case "outline-none":
				return outlineNone.join(" ");
				break;
			default:
				return null;
		}
	}

	return (
		<div className="mt-5">
			<button 
				type="button" 
				className={ retur() }>
					{props.content}
			</button>
		</div>
	);
}

export default Button;
